@import '../../styles/variables';
@import '../../styles/mixin';

.app {
  .forecasts {
    display: flex;
    justify-content: space-around;
    margin: 25px 0;

    .forecast {
      background: $fog;
      border: 1px solid $white;
      border-radius: 8px;
      min-width: 100px;
      margin: 10px;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      align-items: center;
      position: relative;
      width: 100%;

      .__week-name {
        width: 100%;
        text-align: center;
        font-weight: bold;
        font-size: 20px;
      }
      .__week-day {
        width: 100%;
        text-align: center;
        font-size: 16px;
      }

      .__weather-of-day {
        width: 100%;
        .__part-of-time {
          width: 100%;
          margin: 15px 0;
          .__weather-icon {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;

            img {
              height: 50px;
            }
          }
        }
      }

      .__weather-temparature {
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        .__temparature {
          margin: 0 8px;
          white-space: nowrap;
        }
      }
    }

    @include breakpoint($small) {
      display: block;
      .forecast {
        margin: 10px 0;

        .__weather-of-day {
          display: flex;
        }
      }
    }
  }

  &.dark {
    .forecasts {
      .forecast {
        background: $gris;
        border-color: $sunflower;
      }
    }
  }
}
