@import '../../styles/variables';
@import '../../styles/mixin';

#root {
  .app {
    &.dark {
      .ant-layout-header {
        color: $sunflower;
        background-color: $dust;
      }

      .app__content {
        background-color: $gris;
      }
    }

    .ant-layout-header {
      background-color: $silver;
    }

    .app__content {
      height: calc(100vh - #{$header-height});
      padding: 25px 50px;
      overflow-y: auto;
    }

    @include breakpoint($x-small) {
      .app__content {
        padding: 10px;
      }
    }
  }
}
