@import '../../styles/variables';

.app {
  .landing-page {
    background: none;

    .search-container {
      display: flex;
      justify-content: center;

      .search {
        width: 300px;
      }
    }

    .weather-container {
      width: 100%;
      border-radius: 5px;
      background: $fog;
      padding: 25px;
      margin: 25px 0;
      display: flex;
      flex-direction: column;
      align-content: space-between;
      justify-content: space-between;

      .weather-location {
        display: flex;
        justify-content: space-between;
        font-size: 20px;

        .current-location {
          .location-name {
            font-size: 24px;
          }
          .location-country {
            font-size: 16px;
          }
        }

        .location-favorite {
          color: $pumpkin;
          &:hover {
            cursor: pointer;
          }
        }
      }

      .weather-info {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        .weather-icon {
          display: flex;
          justify-content: center;
          align-items: center;

          img {
            height: 100px;
          }
        }
        .weather-temparature {
          font-size: 30px;
        }

        .weather-text {
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
  }

  &.dark {
    .landing-page {
      .weather-container {
        background: $dust;

        .weather-location {
          color: $sunflower;

          .location-favorite {
            color: $sunflower;
          }
        }
      }
    }
  }
}
